export default {
  name: "dealDetailCard",
  props: ['info', 'visible', 'qrcodeValue'],
  methods: {
    close() {
      this.$emit('close');
    },

    beforeClose(done) {
      this.$emit('close');
      done();
    },

    readMoreDeal(info) {
      this.$emit('viewProfile', info);
    },

    share(info) {
      this.$emit('share', info);
    }

  }
};